.footer {
  background-color: $light-grey;
  color: $black;
  display: flex;
  flex-direction: column;
  justify-content: center;

  a {
    color: inherit;
  }

  .footer__wrapper {
    display: flex;
    justify-content: space-between;
    max-width: $wrapper-width;
    align-items: flex-start;
    align-self: center;
    @include divi {
      // flex-direction: column;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
    }
    .column {
      display: flex;
      flex-direction: column;
      @include divi {
        flex-basis: 50%;
      }
      @include big-phone {
        flex-basis: 100%;
      }
      p:not(.footer__title) {
        font-size: 12px;
      }
      &.column-4 {
        width: 35%;
      }
    }

    form {
      display: flex;
      justify-content: center;
      input {
        border: solid 1px $navy;
        background: transparent;
        // height: 48px;
        &[type="text"],
        &[type="email"] {
          padding: 8px;
          border-right: 0;
          font-size: 12px;
          width: 100%;
          // max-width:
          @include ipad {
            width: 180px;
          }
          &::placeholder {
            color: #a7b0b8;
          }
        }
        &[type="submit"] {
          text-transform: uppercase;
          padding: 8px 20px;
        }
      }
    }

    // .logo_wraper {
