    //   }
    // }

    h4 {
      margin-bottom: 42px;
      margin-top: 42px;
      @include divi {
        margin-bottom: 21px;
        margin-top: 21px;
      }
    }

    a,
    p {
      margin-bottom: 20px;
    }
    .icons {
      margin-bottom: 20px;
      a {
        font-size: 21px;
        @include divi {
          &:not(:last-child) {
            margin-right: 15px;
          }
        }
      }

      @include divi {
        text-align: center;
      }

