  .copyright {
    background-color: #052238;
    color: $light-grey;
    font-size: $body;
    padding: 13px 45px;
    font-size: 18px;
    a {
      font-size: 18px;
      transition: all 0.4s ease-in-out;
      color: inherit;
      font-weight: bold;
      &:hover {
